<template>
  <div>
    <core-progress-modal :show="loading" />
    <v-tabs
      v-if="!loading"
      centered
      color="primary"
      dark
      icons-and-text
    >
      <v-tabs-slider color="white" />

      <v-tab href="#dados-cliente">
        Dados Cliente
        <v-icon>mdi-card-account-details</v-icon>
      </v-tab>
      <v-tab href="#carnes">
        Carnês
        <v-icon>mdi-animation</v-icon>
      </v-tab>

      <v-tab href="#pagamentos">
        Pagamentos
        <v-icon>mdi-cash-multiple</v-icon>
      </v-tab>

      <v-tab
        v-if="$root.isRolesPratico()"
        href="#spc">
        SPC
        <v-icon>mdi-account-off-outline</v-icon>
      </v-tab>

      <v-tab
        v-if="$root.isRolesPratico()"
        href="#renegociar">
        Renegociar Dívida
        <v-icon>mdi-handshake</v-icon>
      </v-tab>

      <v-tab-item
        value="dados-cliente"
      >
        <cliente-dados
          :value="form"
          :set-data="setData"
          :editando="true"
          :cobranca="true"
        >
          <!-- A parte da cobrança aparece aqui em baixo -->
          <template
            v-if="$root.isRolesPratico()"
          >
            <v-expansion-panel-content
              :key="99"
              class="primary mt-1"
            >
              <template slot="actions">
                <v-icon color="white">mdi-chevron-down</v-icon>
              </template>
              <template slot="header">
                <span class="title white--text">Procedimentos de Cobrança</span>
              </template>
              <v-card class="elevation-0">
                <v-data-table
                  :headers="headers"
                  :items="items"
                  :total-items="totalItems"
                  :pagination.sync="pagination"
                  class="elevation-1 arrow-pagintion"
                >
                  <template
                    slot="items"
                    slot-scope="{ item }"
                    ma-5>
                    <td class="text-xs-center">{{ item.atrasoDesde | moment("DD/MM/YYYY") }}</td>
                    <td class="text-xs-center">{{ item.dataCobranca | moment("DD/MM/YYYY H:mm") }}</td>
                    <td>{{ item.usuario }}</td>
                    <td class="text-xs-right">{{ item.diasAtraso }}</td>
                    <td class="text-xs-right">{{ formatValorMonetario(item.vlAtrasoOriginal) }}</td>
                    <!-- <td>{{ item.procedimentoCobranca }}</td> -->
                    <td>{{ item.tipoOcorrencia }}</td>
                    <td>{{ item.observacao }}</td>
                    <td class="justify-end layout ma-2">
                      <v-icon
                        v-if="item.id"
                        class="mr-2"
                        color="blue"
                        title="Editar dados da cliente"
                        @click="openEditarOcorrencia(item)">mdi-pencil</v-icon>
                    </td>
                  </template>
                </v-data-table>
                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex md12>
                      <span class="body-2">Procedimento:</span> {{ formCobranca.procedimentoCobranca }} agendado para {{ formCobranca.dataAgendamento | moment("DD/MM/YYYY") }}
                    </v-flex>
                    <v-flex md3>
                      <v-autocomplete
                        v-model="formCobranca.tipoOcorrenciaId"
                        :items="tiposOcorrencia"
                        label="Ocorrência"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      layout
                      align-center
                      class="mt-0 mr-1"
                    >
                      <v-text-field
                        v-model="formCobranca.dataAgendamento"
                        type="date"
                        label="Data Agendamento"
                        reverse
                      />
                      <span class="ml-4 mr-4">às</span>
                      <v-text-field
                        v-model="formCobranca.horaAgendamento"
                        type="time"
                        label="Horário"
                        reverse
                      />
                    </v-flex>
                    <v-flex md6>
                      <v-textarea
                        v-model="formCobranca.observacao"
                        type="text"
                        label="Observação"
                      />
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
          </template>
        </cliente-dados>
        <v-layout class="justify-end mr-4">
          <template
            v-if="isFila"
          >
            <v-btn
              :loading="loading"
              color="primary"
              @click="salvarOcorrencia()">Próximo</v-btn>
            <v-btn
              :loading="loading"
              color="secondary"
              @click="pularCliente()">Pular Cliente</v-btn>
            <v-btn
              outline
              style="color: black !important"
              @click="cancelarFilaCobranca()">Sair</v-btn>
          </template>
          <template
            v-else
          >
            <v-btn
              :loading="loading"
              color="primary"
              @click="salvarOcorrencia()">Agendar</v-btn>
            <v-btn
              outline
              style="color: black !important"
              @click="cancelarCobranca()">Sair</v-btn>
          </template>
        </v-layout>
      </v-tab-item>
      <v-tab-item
        value="carnes"
      >
        <cliente-carnes
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        value="pagamentos"
      >
        <cliente-pagamentos
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        v-if="$root.isRolesPratico()"
        value="spc"
      >
        <cliente-SPC
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        v-if="$root.isRolesPratico()"
        value="renegociar"
      >
        <cliente-renegociar
          :value="form"
        />
      </v-tab-item>
    </v-tabs>
    <template
      v-if="dialogEditOcorrencia"
    >
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialogEditOcorrencia"
          max-width="390">
          <v-card>
            <v-card-title class="headline">Atualizar Observação</v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <v-textarea
                    v-model="item.observacao"
                    label="Observação"
                    counter
                    maxlength="500"
                  />
                </v-flex>
                <v-flex md12>
                  <v-layout class="justify-end">
                    <v-btn
                      color="primary"
                      @click="editarOcorrencia">Salvar</v-btn>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </div>
</template>
<script>
import Endereco from '../../beans/Endereco'
import { ClienteBusiness, SPCBusiness, TiposBusiness, CobrancaBusiness } from '../../business'
import numberUtils from '../../utils/numberUtils'

const ClienteSPC = () => import('../clientes/ClienteSPC')
const ClienteRenegociar = () => import('../clientes/ClienteRenegociar')
const ClienteDados = () => import('../clientes/ClienteDados.vue')
const ClienteCarnes = () => import('../clientes/ClienteCarnes.vue')
const ClientePagamentos = () => import('../clientes/ClientePagamentos')

export default {
  components: {
    ClienteSPC,
    ClienteRenegociar,
    ClienteDados,
    ClienteCarnes,
    ClientePagamentos
  },
  data () {
    return {
      isFila: false,
      item: null,
      dialogEditOcorrencia: false,
      formCobranca: {
        id: null,
        clienteId: null,
        lojaId: null,
        atrasoDesde: null,
        diasAtraso: null,
        vlAtrasoOriginal: null,
        dataCobranca: null,
        procedimentoCobranca: 'Contato telefônico',
        dataAgendamento: null,
        tipoOcorrenciaId: null,
        horaAgendamento: null,
        observacao: null
      },
      tiposOcorrencia: [],
      items: [],
      totalItems: 0,
      pagination: {
        pageNo: 1,
        rowsPerPage: 15,
        sortBy: 'dataCobranca',
        descending: true
      },
      clienteFila: null,
      form: {
        id: null,
        redeId: null,
        empresaId: null,
        lojaId: null,
        nome: null,
        cpf: null,
        identidade: null,
        orgaoEmissor: null,
        dataEmissao: null,
        uf: null,
        grupo: null,
        dataNascimento: null,
        naturalEstado: null,
        naturalCidade: null,
        sexo: null,
        estadoCivil: null,
        dependentes: null,
        nomePai: null,
        nomeMae: null,
        telResidencial: null,
        celular: null,
        email: null,
        escolaridade: null,
        rendaPrincipal: null,
        endereco: new Endereco(),
        tipoResidencia: null,
        tempoResidenciaAnos: null,
        tempoResidenciaMeses: null,
        empresa: null,
        cargo: null,
        tempoEmpregoAnos: null,
        tempoEmpregoMeses: null,
        telComercial: null,
        ramal: null,
        nomeRef1: null,
        relacaoRef1: null,
        telRef1: null,
        nomeRef2: null,
        relacaoRef2: null,
        telRef2: null,
        nomeRef3: null,
        relacaoRef3: null,
        telRef3: null,
        nomeRef4: null,
        relacaoRef4: null,
        telRef4: null,
        vendaId: null,
        observacoes: [],
        ultimoHistoricoSPC: null
      },
      loading: true,
      headers: [
        {
          sortable: false,
          align: 'center',
          text: 'Atraso Desde',
          value: 'dataAtraso'
        },
        {
          sortable: false,
          align: 'center',
          text: 'Cobrança Em',
          value: 'dataCobranca',
          width: '150px'
        },
        {
          sortable: false,
          text: 'Usuário Agenda/Exec.',
          value: 'usuario'
        },
        {
          sortable: false,
          align: 'right',
          text: 'Dias Atraso',
          value: 'diasAtraso'
        },
        {
          sortable: false,
          align: 'right',
          text: 'Atraso Original',
          value: 'atrasoOriginal'
        },
        /* {
          sortable: false,
          text: 'Procedimento de Cobrança',
          value: 'procedimento'
        }, */
        {
          sortable: false,
          text: 'Ocorrência',
          value: 'tipoOcorrencia',
          width: '135px'
        },
        {
          sortable: false,
          text: 'Observação',
          value: 'observacao',
          width: '100%'
        },
        {
          sortable: false,
          text: ''
        }
      ]
    }
  },
  watch: {
    pagination: {
      handler () {
        // this.paginar()
      }
    }
  },
  beforeMount () {
    this.loading = true
    if (this.$route.query && this.$route.query.payload) {
      this.isFila = true
      this.clienteFila = JSON.parse(this.$route.query.payload)
      this.reload(this.clienteFila.clienteId, this.clienteFila.lojaId)
      return
    }
    if (this.$route.params.clienteId && this.$route.params.lojaId) {
      this.isFila = false
      this.reload(this.$route.params.clienteId, this.$route.params.lojaId)
    }
  },
  beforeRouteUpdate (to, from, next) {
    let clienteId = this.clienteFila.clienteId
    let lojaId = this.clienteFila.lojaId
    this.reload(clienteId, lojaId)
    next()
  },
  methods: {
    reload (clienteId, lojaId) {
      this.loading = true
      this.form.id = clienteId
      this.form.lojaId = lojaId
      CobrancaBusiness.getDadosCobrancaCliente(this.form.id, this.form.lojaId)
        .then(response => {
          this.clienteFila = response.data
          ClienteBusiness.getById(this.form.id).then(response => {
            this.form = response.data
            this.form.lojaId = lojaId
            this.formatDatas()
          }).finally(() => {
            if (this.form.status === 'BLOQUEADO') {
              SPCBusiness.findUltimoHistoricoClienteSPC(this.form.id)
                .then(response => {
                  this.form.ultimoHistoricoSPC = response.data
                }).finally(() => {
                  this.loading = false
                })
            } else {
              this.loading = false
            }
          })
          this.paginar()
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loading = false
        })
      TiposBusiness.getAllTiposOcorrencia()
        .then(response => { this.tiposOcorrencia = response.data })
    },
    openEditarOcorrencia (_item) {
      this.item = _item
      this.dialogEditOcorrencia = true
    },
    closeEditarOcorrencia () {
      this.item = null
      this.dialogEditOcorrencia = false
    },
    cancelarCobranca () {
      this.$router.push(`/lista-cliente`)
    },
    salvarOcorrencia () {
      // Cadastrar a ocorrencia/agendamento, e depois chamar o próximo cliente
      this.formCobranca.clienteId = this.form.id
      this.formCobranca.lojaId = this.form.lojaId
      this.formCobranca.atrasoDesde = this.clienteFila.dataVencimento
      this.formCobranca.diasAtraso = this.clienteFila.diasAtraso
      this.formCobranca.vlAtrasoOriginal = this.clienteFila.saldoDevedor
      this.loading = true
      CobrancaBusiness.salvarOcorrencia(this.formCobranca)
        .then(response => {
          this.loading = false
          this.$root.showSucesso(response.data)
          this.resetFormCobranca()
          if (this.isFila) {
            this.pularCliente()
          } else {
            this.$router.push(`/lista-cliente`)
          }
        }).catch(erro => {
          this.loading = false
          this.$root.showErro(erro.data)
        })
    },
    pularCliente () {
      this.clienteFila = this.$root.getProximoFilaCobranca()
      if (this.clienteFila) {
        this.$router.push('/cliente-cobranca?payload=' + JSON.stringify(this.clienteFila))
      } else {
        this.$root.showAlerta('Fila de Cobrança chegou ao fim, informe novos filtros para iniciar uma nova fila.')
        this.$router.push('/cobranca')
      }
    },
    cancelarFilaCobranca () {
      this.$root.cleanFilaCobranca()
      this.$router.push(`/cobranca`)
    },
    paginar () {
      CobrancaBusiness.pagination(this.pagination, {
        clienteId: this.form.id,
        lojaId: this.form.lojaId
      }).then(response => {
        this.items = response.data.content
        this.totalItems = response.data.totalElements
        this.addPreviaOcorrencia()
      })
    },
    addPreviaOcorrencia () {
      let ocorrencia = {
        atrasoDesde: this.clienteFila.dataVencimento,
        dataCobranca: this.$moment(new Date()).add(10, 'm').toDate(),
        usuario: this.$root.usuarioLogin(),
        diasAtraso: this.clienteFila.diasAtraso,
        vlAtrasoOriginal: this.clienteFila.saldoDevedor
      }
      this.items.splice(0, 0, ocorrencia)
      this.totalElements++
    },
    editarOcorrencia () {
      this.formCobranca.id = this.item.id
      this.formCobranca.observacao = this.item.observacao
      this.loading = true
      CobrancaBusiness.salvarOcorrencia(this.formCobranca)
        .then(response => {
          this.loading = false
          this.closeEditarOcorrencia()
          this.resetFormCobranca()
          this.$root.showSucesso(response.data)
        }).catch(erro => {
          this.loading = false
          this.$root.showErro(erro.data)
        })
    },
    setData (data) {
      let lojaId = this.form.lojaId
      this.form = data
      if (lojaId) {
        this.form.lojaId = lojaId
      }
      this.formatDatas()
    },
    formatDatas () {
      if (this.form.dataEmissao) {
        this.form.dataEmissao = this.$moment(this.form.dataEmissao).format('YYYY-MM-DD')
      }
      if (this.form.dataNascimento) {
        this.form.dataNascimento = this.$moment(this.form.dataNascimento).format('YYYY-MM-DD')
      }
      if (this.form.observacoes) {
        this.form.observacoes = this.form.observacoes.map(value => {
          value.dataCadastro = this.$moment(value.dataCadastro).format('YYYY-MM-DD')
          return value
        })
      }
    },
    cancelar () {
      this.$router.push(`/lista-cliente`)
    },
    formatValorMonetario (valor) {
      return numberUtils.formatValorMonetario(valor)
    },
    resetFormCobranca () {
      this.formCobranca = {
        id: null,
        clienteId: null,
        lojaId: null,
        atrasoDesde: null,
        diasAtraso: null,
        vlAtrasoOriginal: null,
        dataCobranca: null,
        procedimentoCobranca: 'Contato telefônico',
        dataAgendamento: null,
        tipoOcorrenciaId: null,
        horaAgendamento: null,
        observacao: null
      }
    }
  }
}
</script>
